.list {
  width: 400px;
  list-style: none;
  margin: 0;
}

.item {
  margin: 10px;
  padding: 5px 10px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
}

.link {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  font-weight: 600;
}

.year {
  font-weight: 600;
  color: lightseagreen;
}
